<template>
  <header class="header sticky-top">
    <div class="container">
      <nav class="navbar">
        <router-link to="/" class="navbar-brand logo">
          Glow<span>Cosmetics</span>
        </router-link>
        
        <button class="navbar-toggler" type="button" @click="toggleNavbar">
          <span class="navbar-toggler-icon">â˜°</span>
        </button>
        
        <div class="navbar-collapse" :class="{ show: isNavbarOpen }">
          <ul class="navbar-nav mx-auto">
            <li class="nav-item">
              <router-link to="/" class="nav-link">Home</router-link>
            </li>
            <li class="nav-item">
              <router-link to="/products" class="nav-link">Products</router-link>
            </li>
            <li class="nav-item">
              <router-link to="/wishlist" class="nav-link">Wishlist</router-link>
            </li>
          </ul>
          
          <div class="d-flex align-items-center">
            <button class="btn position-relative mx-2">
              <i class="far fa-user"></i>
            </button>
            <button class="btn position-relative mx-2" @click="$emit('toggleCart')">
              <i class="fas fa-shopping-bag"></i>
              <span class="cart-count">{{ cartItemCount }}</span>
            </button>
          </div>
        </div>
      </nav>
    </div>
  </header>
</template>

<script>
import { computed, ref } from 'vue'
import { useStore } from 'vuex'

export default {
  emits: ['toggleCart'],
  setup() {
    const store = useStore()
    const isNavbarOpen = ref(false)
    
    const cartItemCount = computed(() => store.getters.cartItemCount)
    
    const toggleNavbar = () => {
      isNavbarOpen.value = !isNavbarOpen.value
    }
    
    return {
      cartItemCount,
      isNavbarOpen,
      toggleNavbar
    }
  }
}
</script>

<style scoped>
.header {
  background-color: white;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.05);
  position: sticky;
  top: 0;
  z-index: 1030;
}

.navbar {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 0;
}

.navbar-brand {
  font-family: 'Playfair Display', serif;
  font-weight: 700;
  font-size: 1.8rem;
  color: var(--primary);
  letter-spacing: 1px;
  text-decoration: none;
}

.navbar-brand span {
  color: var(--accent);
}

.navbar-toggler {
  display: none;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
}

.navbar-collapse {
  display: flex;
  flex-basis: 100%;
  flex-grow: 1;
  align-items: center;
}

.navbar-nav {
  display: flex;
  flex-direction: row;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.nav-link {
  font-weight: 500;
  color: #555;
  position: relative;
  padding: 0.5rem 1.25rem;
  transition: all 0.3s ease;
  text-decoration: none;
}

.nav-link:hover {
  color: var(--primary);
}

/* Underline animation */
.nav-link::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 2px;
  background: var(--primary);
  transition: width 0.3s ease;
}

.nav-link:hover::after,
.router-link-active::after {
  width: calc(100% - 2rem);
}

.cart-count {
  position: absolute;
  top: -5px;
  right: -5px;
  background: var(--primary);
  color: white;
  font-size: 0.7rem;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Responsive adjustments */
@media (max-width: 992px) {
  .navbar-toggler {
    display: block;
  }
  
  .navbar-collapse {
    display: none;
    flex-direction: column;
    width: 100%;
  }
  
  .navbar-collapse.show {
    display: flex;
  }
  
  .navbar-nav {
    flex-direction: column;
    width: 100%;
  }
  
  .nav-item {
    width: 100%;
  }
}
</style>